.contact-us { padding: 30px;}

.contact-us .contact-container .h1 {text-align: center; font-weight: bold; font-size: 30px; text-transform: uppercase; color: black;}

.contact-us .contact-container .contact-input input{     
    padding: 10px;
    border: 1px solid rgb(202, 202, 202);
    border-radius: 7px;
    margin-bottom: 20px;
}

.contact-us .contact-container .contact-input input[type=text]:not(:last-of-type){ width: 45.5%; }

.contact-us .contact-container .contact-input input[type=email]{ width: 93%; }

.contact-us .contact-container .contact-input input[type=text]:last-of-type{ width: 93%;}

.contact-us .contact-container .contact-input textarea { 
    width: 94%;     
    border: 1px solid rgb(202, 202, 202);
    border-radius: 7px;
    height: 180px;
    padding: 10px;
}

.contact-us .contact-container button {
    background-color: black; 
    border: none; 
    text-transform: uppercase; 
    color: white; 
    padding: 15px;
    margin:30px auto;
    display: block;
    letter-spacing: 4px;
    cursor: pointer;
    border-radius: 7px;
}
