footer .icons_container {
    display: inline-flex
}

footer .icons_container .icon {
    margin: 0 10px;
    text-align: center;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    z-index: 2;
    transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55)
}

footer .icons_container .icon span {
    display: flex;
    color: #ce9f2b;
    justify-content: center;
    align-items: center;
    font-size: 19px;
    height: 40px;
    width: 40px;
    background: #fff;
    border-radius: 50%;
    position: relative;
    z-index: 2;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.1);
    transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55)
}

footer .icons_container .icon .tooltip {
    position: absolute;
    top: 0;
    z-index: 1;
    background: #fff;
    color: #fff;
    padding: 10px 18px;
    font-size: 16px;
    font-weight: 500;
    border-radius: 10px;
    opacity: 0;
    pointer-events: none;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.1);
    transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55)
}

footer .icons_container .icon:hover .tooltip {
    top: -60px;
    opacity: 1;
    pointer-events: auto
}

footer .icon .tooltip:before {
    position: absolute;
    content: "";
    height: 15px;
    width: 15px;
    background: #fff;
    left: 50%;
    bottom: -6px;
    transform: translateX(-50%) rotate(45deg);
    transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55)
}

footer .icons_container .icon:hover span {
    color: #fff
}

footer .icons_container .icon:hover span,
footer .icons_container .icon:hover .tooltip {
    text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.4)
}

footer .icons_container .facebook:hover span,
footer .icons_container .facebook:hover .tooltip,
footer .icons_container .facebook:hover .tooltip:before {
    background: #3B5999
}

footer .icons_container .twitter:hover span,
footer .icons_container .twitter:hover .tooltip,
footer .icons_container .twitter:hover .tooltip:before {
    background: #46C1F6
}

footer .icons_container .instagram:hover span,
footer .icons_container .instagram:hover .tooltip,
footer .icons_container .instagram:hover .tooltip:before {
    background: #e1306c
}

footer .icons_container .github:hover span,
footer .icons_container .github:hover .tooltip,
footer .icons_container .github:hover .tooltip:before {
    background: #333
}

footer .icons_container .youtube:hover span,
footer .icons_container .youtube:hover .tooltip,
footer .icons_container .youtube:hover .tooltip:before {
    background: #DE463B
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) { 
    footer .icons_container { margin-right: -10px;}
    footer .icons_container .icon { margin: 0px 5px;}
    footer .icons_container .icon span { height: 30px; width: 30px;}
}


footer .secound-footer p{ font-family: 'Lemonada', cursive; font-size: 11px; color: #848484; margin: 0;}
